@media screen and (min-width:1452px) {
   .gamecontrol{
    background:white;
   }
  }
  
  @media screen and (max-width:500px) {
    #root{
        
    }
    .gamecontrol > *{
        flex-shrink:0;
        height:65% !important;
    }
    .gamecontrol{
        justify-content: center;
        padding:1rem 0rem !important;
    }
    .gamecard .MuiPaper-elevation{
        // outline:1rem solid brown;
        flex-direction: column;
        padding:.8rem 0rem;
    }
    .gamecard .MuiPaper-elevation > img{
        width:80% !important;
    }
    .gamecard .MuiPaper-elevation > div{
        // outline:1rem solid green;
        width:95% !important;
        font-size:80%;
    }
    .gamecard .MuiPaper-elevation > div >h2{
        text-align:center;
    }
  }
  @media screen and (max-width:800px) {
    .gamecontrol{
        grid-row:1 span 2;
        outline:2px solid green;
    }
  }
  @media screen and (min-width:800px) {
    .gamecontrol{
        grid-row:2 / 2;
    }
  }
  root  *{
    gap:.5rem !important;
  }
  *
.GameBox {
    outline:2px solid green;
  }
.GameBox{
    box-sizing:border-box;
    display:grid;
    place-self:start center;color:white;
    width:90% !important;height:80vh !important;
    grid-template-rows: max(60px,10%) auto ;
}
.GameBox .handle{
    clip-path: polygon(8% 1%, 88% 0, 100% 100%, 0 100%);
    display:flex;justify-content: end;
    align-items:center;grid-row: 1 / 1;
    width:40%;height:80%;
    padding-right:.6rem;
    place-self:end;background:white;
    border-top-right-radius: 5em;
    border-top-left-radius: 4.4em;
    overflow:1rem solid purple;

}
.GameBox .game{
    background:#00000083;width:100% !important;
    grid-row: 2 / 2;height:90% !important; 
    outline:.4em solid #ddd;
    overflow:hidden;
}
#connecting {
    background: linear-gradient(45deg,transparent 0%,#ffffff41 50%,transparent 100%),rgba(0,0,0,0.6);
    background-position:0px 0px;animation:wave infinite 3.6s forwards alternate-reverse;
    background-repeat:repeat;background-size:500%;
}
@keyframes wave{
    0%{
        background-position: 150% 150%;
    }
    25%{
        background-position:75% 75%;
    }
    50%{
        background-position:0% 0%;
    }
    50%{
        background-position:75% 75%;
    }
    100%{
        background-position:-150% -150%;
    }
}

.gamecard{
    display:grid;
    place-self:start center;
    width:90% !important;height:90%;
    color:white;
}

.gamecard:first-child{
    grid-row:1 / 1;width:100%;
    height:100%;
    outline:2rem solid orange;
}

.gamecard > .gamecontrol{
    outline:1rem solid orange !important;
    display:flex;align-items: center;
    gap:1em; width:100%;
    height:100%;padding:1rem 10rem;overflow:hidden;
    border-top:1.2em solid white;
    clip-path: polygon(0 0, 100% 0, 87% 100%, 13% 100%); ;
}