@media screen and (min-width:1452px) {
  .hcard {
    /* margin-left:-60rem; */
    height:60vh !important;
    width:fit-content !important;

  }
  .swiper{
    width:100vw !important; 
  }
  .container {
    perspective: 100vw !important;
    width:100vw;top:5vh;
    /* outline:2px solid green; */
  }
}

@media screen and (max-width:400px) {
  .container {
    perspective: 3000vw;
    height: max(300px,80vh) !important;
  }
  .vcard {
    /* padding:3rem; */
    outline: 1rem solid green;
  }
}

@media screen and (min-width:400px) {

  .container {
    perspective: 3000vw;
    height: 100vh !important;
  }
  .hcard {
    perspective: 3000vw;
    height: max(300px,60vh) !important;
  }
}

:root {
  color: #B001DF,
    #FFF205,
    #7CFF01,
    #FE0000;
}
*{
  box-sizing: border-box;
}


.container {
  top: 10vh;
  left: 0vw;
  position: static;
  width: 100vw !important;
  height:100vh important;
  padding-top:.2rem;
}


.container .hcard,
.blankcard {
  transform: scale(1.0);
  background: transparent;
  display: grid;
  border-radius: 1.2rem;
  color: white;
  animation: glow 2s forwards 1;
  grid-template-columns: max(400px,40%) auto;
}

.vcard {
  padding: .3rem;
  background: transparent;
  position: absolute;
  padding-top: .5rem;
  display: flex;
  gap: 1.8rem;
  flex-direction: column;
  border-radius: 1.2rem;
  box-shadow: 0 0 .2rem #fff,
    0 0 .2rem #fff,
    0 0 2rem #B001DF,
    0 0 0.8rem #B001DF,
    0 0 2.8rem #B001DF,
    inset 0 0 1.3rem #B001DF;
  color: white;
  animation: glow 2s forwards 1;
}

.norel {
  mix-blend-mode: difference;
}

