.blankcard {
  display: none;
}

.hcard strong {
  font-size: 2rem;
  text-align: center;
  text-wrap: nowrap;
}

.swiper-slide {
  width: 50% !important;
  height: calc(1.96 * 50% / 1.08) !important;
}

.hcard .cardside {
  /* outline:2px solid green; */
  border-top-left-radius: 1.2rem;
  border-bottom-left-radius: 1.2rem;
  grid-column: 1 / 2;
  width: 80%;
  height: max(80%,240px);
  place-self: center;
  display: flex !important;
  flex-direction: column !important;
  align-items: center;
  padding: 1rem;
}

.vcard .cardside {
  border-top-left-radius: 1.2rem;
  border-bottom-left-radius: 1.2rem;
  margin-left: .5rem;
  grid-column: 1 / 2;
  width: 90%;
  height: 40% !important;
  place-self: center;
  place-items: center;
  display: flex;
  flex-direction: column;
  padding: .3rem;
}

.cardside ul {
  font-weight: bold;
  justify-items: center;
  align-items: center;
  display: flex;
  flex-direction: row;
  list-style-type: none;
  list-style: none;
  margin-right: 10px;
}

.cardside li>svg {
  filter: drop-shadow(2px 2px 5px yellow)
}

.cardside img {
  padding-right: .2rem;
  padding-top: 3rem;
  width: 100%;
  height: auto;
  filter: drop-shadow(-2px -2px 5px white);
}
.cardside img{
  transform: filter 1s ease-in;
  filter: blur(2px);
}

.cardside:hover {
  box-shadow: inset 20px black;
}

.hcard article,
.vcard article {
  border-top-right-radius: 1.2rem;
  border-bottom-right-radius: 1.2rem;
  padding-top: .4rem;padding-right: .8rem;
  font-family: Bahnschrift;
  font-size: 1.4rem;
  grid-column: 2 / 2;
  place-self: center;
  width: 100% !important;
  height: 80%;
  text-align: justify;
  text-shadow: -3px -3px 9px white;
  overflow-y: scroll;
  /* box-shadow:2px 2px 15px #272727 inset; */
}

.vcard article {
  text-align: center;
}

.hcard article::-webkit-scrollbar,
.vcard article::-webkit-scrollbar {
  display: none;
}