@font-face {
	font-family: gb-robo;
	src: url("../assets/fonts/Oswald-VariableFont_wght.ttf");
}

$col1: hsl(54, 51%, 71%);
$col2: hsl(300, 35%, 35%);

@media screen and (max-width:500px) {
	#authBox {
		gap: 5%;
	}

	#authForms .authFields {
		width: 100% !important;
		flex-shrink: 0;
	}

	#authBox #banner {
		display: none;
	}

	#authForms form {
		scroll-snap-type: x proximity;
		overflow-x: scroll;
		height: 100%;
		justify-content: none !important;
	}

	#authForms form>* {
		scroll-snap-align: center;
		height: 100% !important;
	}
}

@media screen and (min-width:500px) and (max-width:1050px) {

	#authBox {
		height: 80%;
		gap: 5%;
	}

	#authBox #banner {
		display: none;
	}
	#authForms .authFields {
		width: 100% !important;
		flex-shrink: 0;
	}

	#authForms form {
		scroll-snap-type: x proximity;
		overflow-x: scroll;
		height: 100%;
		// justify-content: none !important;
	}
}

@media screen and (min-width:1050px) {
	#authForms .authFields {
		width: 50%;
	}

	#authForms {
		width: 100%;
	}

	#authBox #banner {
		display: grid;
	}

	#authForms>form {
		justify-content: space-between;
	}
}

nav {
	position: sticky;
	z-index: 4;
	outline: 2px double white;
	height: 10vh;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-inline: 1rem;
	background-color: transparent;

	#brand {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		width: 15%;

		span {
			color: white;
			font-size: 3rem;
			font-family: gb-robo, sans-serif;
			font-weight: 600;
			font-style: normal;
			background: #7a35cf;
			background: linear-gradient(to right, #7a35cf 0%, #dbd38f 100%);
			background-clip: text;
			-webkit-text-fill-color: transparent;
			display: flex;
			justify-content: center;
			align-items: center;

			img {
				width: 3rem;
				transform: translateY(25%);
			}
		}
	}

	#nav-control {
		display: flex;
		padding: 0;
		align-items: center;

		.text {
			color: white;
			margin-block: 8px;
		}

		.avatar {
			font-size: 1.5rem;
		}
	}

	#ppic {
		height: 4rem;
		aspect-ratio: 1;
		border-radius: 50%;
	}

	#ppic:hover {
		filter: brightness(80%);
		cursor: pointer;
		transition: all 50ms ease-in;
	}

	#pmenu {
		position: absolute;
		padding: .2rem 1rem;
		width: min-content;
		transform: translate(60%, 80%);
		flex-direction: column;
		align-items: center;
		;
		gap: 1rem;
		aspect-ratio: calc(1);
		background: #272727;
		color: white;
		z-index: 5;
		border-radius: 1rem;
		border: .2rem double white;
	}

	#pmenu>span {
		display: flex;
		flex-direction: column;
		text-align: center;
	}

	#pmenu strong {
		color: #ddd;
		transform: scale(1.05);
	}
}

#oauth {
	display: flex;
	justify-content: space-between;
}

.pixel-font {
	font-optical-sizing: auto;
	font-weight: 400;
	font-style: normal;
}

#authSign {
	transition: all .8s ease-in-out;
}

#authSign>#authBox>* {
	height: 100%;
	width: 100%;
}

#authSign>#authBox>div:not(:has(form)) {
	overflow: hidden;
}

#authSign>#authBox {
	display: grid;
	grid-template-rows: max(80px, 10%) auto;
}

#authSign #authHeader {
	font-family: bahnschrift;
	font-size: 2.4rem;
	place-items: center;
	text-align: center;
}

#authForms {
	overflow-y: scroll;
	background: white;
}

#authForms>form {
	display: flex;
}

#authForms *::-webkit-scrollbar {
	display: none;
}

#authSign .authFields {
	display: flex;
	flex-direction: column;
	gap: .4rem;
	padding-top: .4rem;
	align-items: center;
	border: none;
}

#authSign .authFields>span {
	display: flex;
	place-content: center;
}

.login {
	justify-content: start;
	gap: max(2rem, 10%) !important;
	overflow-y: hidden;
	background: #272727;
	color: white;
}

.authFields>* {
	flex-shrink: 0;
	width: 60%;
	display: flex;
	justify-content: space-between;
}

.authFields label {
	width: 40%;
}

.authFields:not(.login) .MuiTextField-root {
	background: #272727;
	color: white;
}

.authFields.login .MuiTextField-root {
	background: white;
	color: #272727;
}

.MuiTextField-root {
	border-radius: .4rem;
	width: 70%;
}

#authBox #banner {
	position: absolute;
	grid-row: 2;
	width: 50%;
	height: 91%;
	padding: 0;
	margin: 0;
	background: #272727;
	box-shadow: -5px 0px 15px 1px #aaaaaa;
	transition: all .6s ease-in-out;
	z-index: 10;
}

#authBox #banner span {
	height: 60%;
	aspect-ratio: 1;
	place-self: center;
}

#messbox {
	transform: translateY(-100%);
	animation-name: dropmodal, raisemodal;
	animation-delay: 0s, 3s;
	animation-duration: .9s, 1s;
	// animation-timing-function: ease-in;
	animation-fill-mode: forwards;
}

@keyframes dropmodal {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
		transform: translateY(10%);
	}
}

@keyframes raisemodal {
	to {
		opacity: 0;
	}
}