* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	// outline: 2px solid limegreen;
}
$brand-bg: hsl(222, 12%, 16%);

body {
	font-family: gb-robo,-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	margin-top: 1rem;
	height: 100svh;
	display: grid;
}
#root{
	display: grid;
	gap: 2rem;
	width: 100%;
	height: 100svh;
	padding:10px;
}

html {
	height: 100svh;
	--sz: 7px; /*** size ***/
	--c0: hsl(220, 39%, 7%);
	--c1: hsl(240, 20%, 42%);
	--c2: hsl(240, 9%, 24%);
	--c3: hsl(240, 12%, 11%);
	--ts: 50% / calc(var(--sz) * 12.8) calc(var(--sz) * 22);
	background: conic-gradient(
				from 120deg at 50% 86.5%,
				var(--c1) 0 120deg,
				#fff0 0 360deg
			)
			var(--ts),
		conic-gradient(
				from 120deg at 50% 86.5%,
				var(--c1) 0 120deg,
				#fff0 0 360deg
			)
			var(--ts),
		conic-gradient(
				from 120deg at 50% 74%,
				var(--c0) 0 120deg,
				#fff0 0 360deg
			)
			var(--ts),
		conic-gradient(
				from 60deg at 60% 50%,
				var(--c1) 0 60deg,
				var(--c2) 0 120deg,
				#fff0 0 360deg
			)
			var(--ts),
		conic-gradient(
				from 180deg at 40% 50%,
				var(--c3) 0 60deg,
				var(--c1) 0 120deg,
				#fff0 0 360deg
			)
			var(--ts),
		conic-gradient(from 0deg at 90% 35%, var(--c0) 0 90deg, #fff0 0 360deg)
			var(--ts),
		conic-gradient(
				from -90deg at 10% 35%,
				var(--c0) 0 90deg,
				#fff0 0 360deg
			)
			var(--ts),
		conic-gradient(from 0deg at 90% 35%, var(--c0) 0 90deg, #fff0 0 360deg)
			var(--ts),
		conic-gradient(
				from -90deg at 10% 35%,
				var(--c0) 0 90deg,
				#fff0 0 360deg
			)
			var(--ts),
		conic-gradient(
				from -60deg at 50% 13.5%,
				var(--c1) 0 120deg,
				#fff0 0 360deg
			)
			var(--ts),
		conic-gradient(
				from -60deg at 50% 13.5%,
				var(--c1) 0 120deg,
				#fff0 0 360deg
			)
			var(--ts),
		conic-gradient(
				from -60deg at 50% 41%,
				var(--c2) 0 60deg,
				var(--c3) 0 120deg,
				#fff0 0 360deg
			)
			var(--ts),
		var(--c0);
	box-shadow: 
		inset 40vmin 40vmin 50vmin var(--c0),
		inset -40vmin -40vmin 50vmin var(--c0);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}
