@import url('https://fonts.googleapis.com/css2?family=Mukta:wght@800&display=swap');

@media screen and (max-width:700px){
    .TicTacToe{
        display:flex;flex-direction: column;
        align-items: center;
    }
}
@media screen and (min-width:700px){
    .TicTacToe{
        display:grid;
    }
}
.TicTacToe{
    grid-row:1 / 1;
    padding: 0rem 1rem;
    grid-template-columns: 60% auto;
    /* outline:2px solid red; */
    /* display:none; */
}
.TicTacToe .board{
    aspect-ratio:1;
    grid-column:1 / 1;grid-row:1 / 1;
    height:clamp(400px,90%,60vh) !important;display:grid;
    background:#ffffff09;
    animation: spread 1 1.6s ease-in-out forwards;
}


.board span{
    font-family:Mukta,Bahnschrift;font-style:normal;font-weight:800;
    aspect-ratio:1;overflow:hidden;
    place-content:center;text-align:center;
    transition:all .15s ease-in-out;
    height:100%;font-size: 500% !important;
}
.board .win{
    animation:victory .5s ease-out forwards;
}
.board .loss{
    animation:defeat .5s ease-out forwards;
}
@keyframes victory {
    to{
        background:#33ff00;color:white;
        border: #33ff00;
    }
}
@keyframes defeat {
    to{
        background:#f90009a3;color:white;
        border: #f90009a3;
    }
}
@keyframes spread {
    from{
        gap:0%;
    }
    to{
        gap:3.5%;
    }
}
.board span:hover{
    background:#ffffffa4;color:grey;
    content:'x';cursor:pointer;
}